import { HttpService } from 'src/services/http/http.service';
import { IUser } from './../../../interfaces/i.user.interface';
import { SharedComponents } from 'src/app/shared.components';
import { CurrentUserService } from './../../../services/authentcation/current.user.service';
import { Component, OnInit } from '@angular/core';
import { SEOService } from 'src/services/seo.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AlertService } from 'src/services/alert.service';
import { FileDragHandle } from 'src/interfaces/file.drag.handel.interface';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { SweetAlertService } from 'src/services/sweet.alert.service';
import { CustomValidatorsService } from 'src/services/custom.validators.service';
import { userTypesArray } from 'src/consts/user.type.const';

@Component({
  selector: 'app-create-user',
  templateUrl: './create.user.component.html',
  styles: [
  ]
})
export class CreateUserComponent extends SharedComponents implements OnInit {
  pageTitle: string = "createANewUser";

  formSubmitError: boolean;
  userInfo = {} as IUser;
  newPicture = {} as FileDragHandle;
  defaultImageUrl = `${environment.api_url}files/users/default.png`;
userTypes=userTypesArray ;

  form = new FormGroup({
    userName: new FormControl(null, [Validators.required, CustomValidatorsService.userName]),
    fullName: new FormControl(null, Validators.maxLength(100)),
    email: new FormControl(null, [ Validators.email]),
    phoneNumber: new FormControl(null, CustomValidatorsService.phoneNumber),
    userTypeId: new FormControl(null, Validators.required),
    password: new FormControl(null, [Validators.required, Validators.maxLength(50), Validators.minLength(5)]),
  });
  constructor(public u: CurrentUserService, private seoService: SEOService, private http: HttpService, private router: Router, private sweetAlertService: SweetAlertService) { super() }

  ngOnInit(): void {
    this.seoService.updateSEO(this.pageTitle);
  }


  /**
   * Save Change
   */
  saveChange(): void {
debugger
    //Check Form Is Valid
    if (this.form.invalid) {
      AlertService.errorMessage(this.u.r.youHaveSomeErrors);
      this.formSubmitError = true;
      return;
    }
    this.formSubmitError = false;

    this.userInfo.actionDisapled = true;
    this.form.disable();
    let fomrD = new FormData();
    fomrD.append('values', JSON.stringify(this.form.value));
    if (this.newPicture.file)
      fomrD.append('image', this.newPicture.file);


    this.allSubscriptions.push(
      this.http.createUser<IUser>(fomrD).subscribe(res => {
        this.userInfo.actionDisapled = false;
        if (res.isSuccess) {
          let userNameCreated = this.form.controls.userName.value;
          this.sweetAlertService.confirm(this.u.r.doYouWantEditCreatedItem, this.u.r.goToEdit, this.u.r.createNew).then(() => {

            this.router.navigate(['/users', 'edit', userNameCreated]);
          })
          this.form.reset();
        }
        AlertService.showResponseMessage(res);
        this.form.enable();
        this.userInfo.actionDisapled = false;

      }, error => {
        AlertService.canRequestError(error)
        this.form.enable();
        this.userInfo.actionDisapled = false;
      }, () => {

      }));
  }

  /** Show And Hide Password Toggel */
  showPasswordToggal(elementTrget: HTMLInputElement) {
    if (elementTrget.type == "password")
      elementTrget.type = "text";
    else
      elementTrget.type = "password";
  }

}//End Class
